import { h, Component } from 'preact';
import { Router } from 'preact-router';
import Helmet from "preact-helmet";

// Code-splitting is automated for routes
import Home from '../routes/home';
import ErrorType from '../routes/error';
import InstaMotion from '../routes/instamotion';
import CodePen from '../routes/codepen';
import RedesignIM from '../routes/redesignim';

export default class App extends Component {
	
	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.currentUrl = e.url;
		scrollToTop();
	};
	
	render() {
		return (
			<div id="app">
				<Helmet 
					title="Andrey Vasenko" 
					link={[
						{ "rel":"stylesheet", "href":"https://use.fontawesome.com/releases/v5.7.2/css/all.css", "integrity":"sha384-fnmOCqbTlWIlj8LyTjo7mOUStjsKC4pOpQbqyi7RrhN7udi9RwhKkMHpvLbHG9Sr", "crossorigin":"anonymous"}
					]}
				/>
				<Router onChange={this.handleRoute}>
					<Home path="/" />
					<InstaMotion path="/instamotion"/>
					<CodePen path="/codepen"/>
					<RedesignIM path="/redesign"/>
					<ErrorType type="404" default />
				</Router>
			</div>
		);
	}
}

function scrollToTop() {
	if (typeof window !== "undefined") {
		document.body.scrollTop = 0;
		document.documentElement.scrollTop = 0;
	}
}


