import { h } from 'preact';
import style from './style';

import { Link } from 'preact-router/match';

import Header from '../../components/header';

const RedesignIM = () => (
	<div class={style.projectWrapper}>
	<Header/>
		<p class={style.note}>
			<span>Project</span> <br/>
			InstaMotion, Bachelors Thesis
		</p>
		<p class={style.note}>
			<span>Role</span> <br/>
			UI/UX Design, Illustration, HTML,&nbsp;CSS,&nbsp;JS
		</p>
		<div class={style.projectPart}>
		
			<h1>Redesign of InstaMotion.com</h1>
			<p class={style.projectPart__details}>
				In this project, I worked with Hotjar and Google Analytics to collect and analyse user data. 
				(Like user feedback, statistics, heatmaps, and recordings.) And with Sketch, Figma, CSS, HTML, JS to create designs and mockups.
			</p> 
			<div class={style.picContainer}>
				<div class={style.pic}>
					<img style="box-shadow: none;" src={"/assets/img/preview-ba.png"} />
				</div>
			</div>
			
		</div>

		<div class={style.projectPart}>
			<h2>Homepage and Search</h2>
			<p class={style.projectPart__details}>
				When researching user activity, I found out that using a text input field as the main search option was confusing for the user.
				Visitors would often copy the text from the placeholder, type in a car make or model and then spend more time readjusting the filters.
			</p> 
			<p class={style.projectPart__details}>
				I've made it easier to access general categories and used free text search as an alternative to a more conventional variant. 
			</p> 
			<div class={style.picContainer}>
				<div class={style.pic}>
					<img src={"/assets/img/im-redesign-1.png"} />
				</div>
				<div class={style.pic}>
					<img src={"/assets/img/im-redesign-2.png"} />
				</div>
			</div>
		</div>

		<div class={style.projectPart}>
			<h2>SEO Content Design</h2>
			<p class={style.projectPart__details}>
				According to Google Analytics, many of InstaMotion users are interested in reading car related articles. 
				To make SEO texts more attractive for them, I used "magazine" and "newspaper" looks as inspiration.
			</p> 
			<div class={style.picContainer}>
				<div class={style.pic}>
					<img style="box-shadow: none;" src={"/assets/img/im-redesign-3.png"} />
				</div>
			</div>
		</div>

		<div class={style.projectPart}>
			<h2>Animation and Interaction</h2>
			<p class={style.projectPart__details}>
				I used a combination of overall minimalistic design and interesting interactive elements 
				to make the website more responsive to the user's actions and be easy to navigate.
			</p> 
			<div class={style.picContainer}>
				<div class={style.pic}>
					<img style="box-shadow: none;" src={"/assets/img/im-redesign-5.gif"} />
				</div>
				<div class={style.pic}>
					<img style="box-shadow: none;" src={"/assets/img/im-redesign-6.gif"} />
				</div>
			</div>
			<div class={style.picContainer}>
				<div class={style.pic}>
					<img style="box-shadow: none;" src={"/assets/img/im-redesign-4.gif"} />
				</div>
			</div>

		</div>

		<div class="buttons">
			<Link href="/codepen/"><i class="fas fa-angle-left"></i>&nbsp;PREVIOUS PROJECT</Link>
			<Link href="/instamotion/">NEXT PROJECT&nbsp;<i class="fas fa-angle-right"></i></Link>
		</div>

	</div>
);
	
export default RedesignIM;
	


